*{
  box-sizing: border-box;
  margin:0 0;
  overflow: hidden;
}
.App {
  text-align: center;
}
.container {
  min-height: 100vh;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  color: white;


}
h6{
  filter: drop-shadow(2px 2px 2px #fff);
}
.bg{
  z-index: -1;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100vh;
}
.profile{
  z-index: 3;
  width: 50%;
  display: flex;
  flex-direction: row;
  background-color:#333;
  box-sizing: border-box;
  opacity: 0.9;
  border: #cc00ff  solid 2px;
  position: relative;
}
.bgprofile{
  z-index: -1;
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  opacity: 0.5;
  filter: contrast(130%);
}
.banner{
 min-width: 25%;
 
 height: 500px;
 display: flex;
 position: relative;
 box-sizing: border-box;
 
}
.flag{
  z-index: 2;
  position: absolute;
  width: 100%;
  height: 90%;
  background-color: #222222;
  clip-path: polygon(50% 100%, 100% 90%, 100% 0, 0 0, 0 90%);
  animation: flaganimation 2s infinite alternate;

  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;

}
.flagback{
  z-index: 1;
  position: absolute;
  width: 100%;
  height: 90%;
  background-color: #444444;
  clip-path: polygon(50% 88%, 100% 96%, 100% 0, 0 0, 0% 96%);
  opacity: 0.5;
  animation: flaganimation 2s infinite alternate;
}
.content{
  min-width: 75%;
  height: 500px;
  display: flex;
  align-items:flex-end;
 
 
}
.footer{
 padding: 30px;
 width: 100%;
 display: flex;
 justify-content: space-evenly;
}
.elo{
  display: flex;
  flex-direction: column;

}
.eloimg{
  width: 120px;
  animation: eloanimation 2s infinite alternate;
  filter: drop-shadow(1px 1px 1px rgba(255, 255, 255, 0.9));
}

.level{
 padding: 30px;
}
.borderlevel{
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid #666;
  width: 40px;
  border-radius: 4px;
}
.icone{
  width: 100px;
  height: 100px;
  border-radius: 100%;
  background-position: center;
  background-repeat: no-repeat; 
  background-size: 100px;
  border: #cc00ff  solid 2px;

}
.borderimg{
  width: 100px;
}
.iconeimg{
  width: 100px;
  z-index: 5;
}
.essenceblue{
 display: flex;
 flex-direction: row;
 align-items: center;
 
}
.essenceblueimg{
  width: 50px;
  filter: drop-shadow(2px 2px 4px rgba(255, 255, 255, 0.9));
}
.essenceorange{
  display: flex;
  flex-direction: row;
  align-items: center;

 }
 .essenceorangeimg{
   width: 50px;
   filter: drop-shadow(2px 2px 4px rgba(255, 255, 255, 0.9));
 }
 .stats{
  display: flex;
 
  justify-content: center;
  align-items: center;
  flex-direction: column;
 }
@keyframes flaganimation{
  0% {

      transform: translateY(-20px);
  }
  100% {
  
      transform: translateY(0px);
  }
}
@keyframes eloanimation{
  0% {
      transform: scale(1);
  }
  100% {
      transform: scale(1.2);
  }
}
